import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatSort } from '@angular/material';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { SearchObject, SearchObjectTreatment } from '@app/shared';
import { Doc } from '@app/shared/entites/document/Doc';
import { VDoc } from '@app/shared/entites/document/VDoc';
import { ConfigService } from '@app/shared/services/config.service';
import { DocumentService } from '@app/shared/services/document/document.service';
import { TranslateService } from '@ngx-translate/core';
import { AddEditDocumentComponent } from './add-edit-document/add-edit-document.component';
import { CONSTANTE_WS, CONSTANTE } from '@app/shared/constante';
@Component({
  selector: 'app-document',
  templateUrl: './document.component.html',
  styleUrls: ['./document.component.scss']
})
export class DocumentComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild('matSort', { static: false }) sort: MatSort;

  @Input() idSrc: number;
  @Input() typSrc : string =" ";

  dataSource: MatTableDataSource<VDoc>;
  rows = [];
  countPage: Number;
  temp = [];
  searchObject: SearchObject;
  displayedColumns: string[] = ['libelleTypeDoc', 'libelleNatureDoc', 'nomFichier', 'nbreCopie', 'action'];
  listeDoc: VDoc[];
  documentById: Doc;
  selectedDocument: Doc;
  currentLang: string;
  currentlistDoc: Doc;
  public config: any = {};

  constructor(private documentService: DocumentService, private searchObjectTreatment: SearchObjectTreatment,
    private configService: ConfigService,
    private translate: TranslateService,
    private dialog: MatDialog,) {
    this.translate.onDefaultLangChange.subscribe((language) => {
      this.currentLang = language.lang;
    });

  }

  ngOnInit() {
    this.config = this.configService.templateConf;
    this.currentLang = this.translate.getDefaultLang();
    this.searchObject = this.searchObjectTreatment.treatementSearchObject(5, 0, "dtMaj", "desc nulls last");
    this.initDataListDocument();
/* 
     this.documentService.documentAddedSubject.subscribe((data) => {
      this.dataSource = null;
      this. getListDocument(); 
    }) */ 

  }

  public initDataListDocument() {
    debugger
    this.documentService.getListDocumentWithFiltre(this.idSrc, this.typSrc, this.searchObject).subscribe(
      response => {
        if (response['code'] === CONSTANTE_WS.SUCCESS) {
          this.countPage = response['payload']['total'];
          this.rows = response['payload']['data'];

          this.countPage = response['payload']['total'];

          this.dataSource = new MatTableDataSource<VDoc>(
            response['payload']['data']
          );
          console.log(this.dataSource);
          this.temp = this.rows;
        }
      },
      error => {
        console.log('error :: ' + error);
      }
    )
  }

  public getListDocument() {
    this.documentService.getListDocumentWhitoutFilte().subscribe(
      response => {
        if (response['code'] === '200') {
          this.listeDoc = response['payload'];
          this.dataSource = new MatTableDataSource<VDoc>(this.listeDoc);
          console.log("listeeeentite", this.listeDoc);
        }
      },
      error => {
        console.log('error :: ' + error);
      }
    )


  }

  public getDocById(id) {
    this.documentService.getListDocumentById(id).subscribe(response => {
      if (response['code'] === '200') {
        this.documentById = response['payload'];
      }
    }, err => {
      console.log(err);
    })
  }

  public getRow(doc: Doc) {
    this.selectedDocument = doc;
    if (this.selectedDocument.id != null) {
      this.getDocById(this.selectedDocument.id);

    }

  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue;
  }

  public onPaginateChange(event): void {
    this.searchObject.pagination.offSet = event['pageIndex'];
    this.searchObject.pagination.limit = event['pageSize'];
    this.initDataListDocument();
  }

  onAddDocument() {
    debugger ; 
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '65%';
    dialogConfig.direction = this.currentLang === 'ar' ? 'rtl' : 'ltr';
    dialogConfig.closeOnNavigation = true;
    dialogConfig.hasBackdrop = true;
    dialogConfig.data = {
      idSrc: this.idSrc,
      typSrc: this.typSrc,
      fedit:true ,
      fadd:false

    }
    console.log("ffffffffffffffff", dialogConfig.data) ; 
    const dialogRef  = this.dialog.open(AddEditDocumentComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.status === 'OK') {
        this.searchObject = this.searchObjectTreatment.treatementSearchObject(5, 0, "dtMaj", "desc nulls last");
        this.initDataListDocument();
      }
    });

  }

  onEditDocument() {
    debugger;
    let docModif;
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = true;
    dialogConfig.autoFocus = true;
    dialogConfig.width = '65%';
    dialogConfig.direction = this.currentLang === 'ar' ? 'rtl' : 'ltr';
    dialogConfig.closeOnNavigation = true;
    dialogConfig.hasBackdrop = true;
    //docModif = {};
    this.currentlistDoc = { ...this.selectedDocument };
    docModif = this.currentlistDoc;
    dialogConfig.data =  {
       docModif, 
       idSrc: this.idSrc,
       typSrc: this.typSrc,
       fedit:false ,
       fadd:true
    }
    

   
    const dialogRef  = this.dialog.open(AddEditDocumentComponent, dialogConfig);

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.status === 'OK') {
        this.searchObject = this.searchObjectTreatment.treatementSearchObject(5, 0, "dtMaj", "desc nulls last");
        this.initDataListDocument();
      }
    });
  
  }
}